import React from 'react';
import { generateRandomString } from '../util.js';

const Dialog = props => {

    let { title, titleId, contentId, success, error, ...otherProps } = props;

    let className = "rocket__dialog";

    if ( error ) {

        className += ' rocket__dialog--error';
    }
    else if ( success ) {

        className += ' rocket__dialog--success';
    }

    return  <div className={ className }
                 role="dialog" 
                 aria-labelledby={ titleId } 
                 aria-describedby={ contentId }
                 { ...otherProps }
            >
                <h4 className="rocket__dialog__title" id={ titleId }>{ title }</h4>
                { props.children }
            </div>
}

Dialog.defaultProps = {

    title: '',
    titleId: 'dialog-title-' + generateRandomString(),
    contentId: ''
}

const Cross = props => {

    return  <button className="rocket__dialog__cross" 
                    role="button"
                    aria-label={ props['aria-label'] }
                    { ...props }
            >
            </button>
}

Cross.defaultProps = {

    'aria-label': 'Close'
}

const Content = props => {

    return  <p className="rocket__dialog__content" 
               id={ props.id }
               { ...props }
            >
                { props.children }
            </p>
}

const Buttons = props => {

    return  <div className="rocket__dialog__buttons" { ...props } >{ props.children }</div>
}

Dialog.Cross = Cross;
Dialog.Content = Content;
Dialog.Buttons = Buttons;

export {

    Dialog
}